import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";

const { SearchBar } = Search;
class SelisihTimbangRosok extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "kode",
          text: "Kode",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "harga",
          text: "Harga",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadar",
          text: "Kadar",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "gross",
          text: "Gross (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "pkg",
          text: "Pkg (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "berat",
          text: "Berat (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "harga",
          text: "Harga",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "netto",
          text: "Netto (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "ongkos",
          text: "Ongkos (Rp)",
          sort: true,
          headerStyle: () => {
            return { width: "25%" };
          },
        },
      ],
      data: [
        {
          tanggal: "2020-11-10",
        },
      ],
    };
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="card">
          <div className="card-body">
            <div className="row">
              <div className="col-lg-3">
                <div className="form-group">
                  <label htmlFor="">Tanggal</label>
                  <input
                    type="date"
                    name="tanggal"
                    label="tanggal"
                    className="form-control"
                  />
                </div>
              </div>
              <div className="col-lg-3">
                <div className="form-group">
                  <label htmlFor="">Lokasi</label>
                  <select
                    name="tanggal"
                    label="tanggal"
                    className="form-control"
                  >
                    <option value="DEFAULT" key="DEFAULT">
                      SILAHKAN PILIH
                    </option>
                  </select>
                </div>
              </div>
              <div className="col-lg-3">
                <div className="form-group">
                  <label htmlFor="">Kriteria</label>
                  <div className="form-control-transparant">
                    <label className="mr-3">
                      <input
                        id="kodeJenis"
                        name="kriteria"
                        type="radio"
                        value="Kode Jenis"
                        className="mr-2"
                      />
                      Kode Jenis
                    </label>
                    <label>
                      <input
                        id="kadar"
                        name="kriteria"
                        type="radio"
                        value="Kadar"
                        className="mr-2"
                      />
                      Kadar
                    </label>
                  </div>
                </div>
              </div>
              <div className="col-lg-3">
                <div className="form-group">
                  <label htmlFor="">Detail Data</label>
                  <select
                    name="detailData"
                    id="detailData"
                    className="form-control"
                  >
                    <option value="SEMUA" key="SEMUA">
                      SEMUA
                    </option>
                  </select>
                </div>
              </div>
              <div className="col-lg-12 mt-3">
                <div className="text-right">
                  <button className="btn btn-primary">Lihat Data</button>
                </div>
              </div>
              <div className="col-lg-12">
                <ToolkitProvider
                  keyField="kodeKelompok"
                  data={this.state.data}
                  columns={this.state.columns}
                  search
                >
                  {(props) => (
                    <div className="form-group">
                      <div className="text-right mt-5 mb-3">
                        <SearchBar
                          {...props.searchProps}
                          className="form-control"
                          placeholder="Tuliskan Kode / Nama"
                          style={{ margin: 0, top: "50%" }}
                        />
                      </div>
                      <BootstrapTable
                        {...props.baseProps}
                        striped
                        hover
                        condensed
                        bordered={true}
                        headerClasses="header-table"
                        pagination={paginationFactory()}
                      />
                    </div>
                  )}
                </ToolkitProvider>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default SelisihTimbangRosok;
